ot in realizing my abilities and more importantly \n          my problem areas. Pushkar’s course almost had all the solutions of sall my problems. I joined the course \n          with a motive to develop self-confidence and communication skills which I required to have before starting\n          my job as I got placed in TCS (Tata Consultancy Services). Now when I am working, I am realizing the importance\n            of the course which I did. I am more confident, efficient and I know my strengths and weaknesses. \n            Personality development is a life-long process and it requires a lot of efforts and 'consciousness' \n            from one's side, but the personality development course gave this process a right direction and the \n            motivation it requires. Rest depends on the individual. Thanks Pushkar for your support and help. Thanks a lot.")])])])])])}]

